.sideMenuContainer{position:fixed;z-index:10;right:calc((100vw - 1920px)/2 + 10px);background:rgba(255,252,227,.15);border:3px solid #ebe6c4;box-shadow:2px 2px 10px rgba(61,62,61,.1);backdrop-filter:blur(12px);border-radius:100px;top:50%;transform:translateY(-50%)}.sideMenuContainer .linkContainer{display:flex;flex-direction:column;gap:32px;padding:32px 20px}.sideMenuContainer .link{width:36px;height:36px;border:none}.sideMenuContainer .link svg{width:36px;height:36px;transition:color .2s ease-out;color:#161616}.sideMenuContainer .link svg:hover{color:#414141}@media(max-width: 749px){.sideMenuContainer{display:none}}