.container{max-width:1680px;width:100%;height:100%;margin-inline:auto}.container .small{padding-block:22px}.container .medium{padding-block-start:98px;padding-block-end:98px}@media(max-width: 749px){.container{padding-inline-start:12px;padding-inline-end:12px}}@media(min-width: 750px){.container{padding-inline-start:45px;padding-inline-end:45px}}@media(min-width: 1024px){.container{padding-inline-start:80px;padding-inline-end:80px}}@media(min-width: 1800px){.container{padding-inline-start:0px;padding-inline-end:0px}}